* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul,ol {
  list-style: none;
}

body {
  min-height: 100vh;
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  color: dimgrey;
}

.show-card {
  color: blue;
  background-color: crimson;
  padding: 5px;
  border: 1px solid white;
}

.show-card--favourite {
  color: crimson;
  background-color: blue;
}

.preview--favourite {
  background-color: yellow;
  width: 50px;
}

.preview--favourite img {
  width: 100%;
}

.preview--favourite h3 {
  font-size: 12px;
}

.favourites {
  margin-right: 50px;
}

.favourites__title {
  color: crimson;
}

.results__title {
  color: crimson;
}

.app-result {
  display: flex;
}
